//GameManager.ts
import { _decorator, Component, Node, Prefab, math, v3, instantiate, tween } from 'cc';
const { ccclass, property } = _decorator;

export enum EnumGameStatus{//游戏状态
    init=1,//初始化
    idle=2,//游玩
    pause=3,//暂停
    die=4,//死亡
}
@ccclass('GameManager')
export class GameManager extends Component {
    
    //障碍物
    @property(Prefab)
    private pref_Brick:Prefab = null!
    //障碍物生成节点
    @property(Node)
    private nd_BrickPlace:Node = null!
///////////////////////////////////////////////
    //计时器
    private Timer = 0
    //所有砖块
    private allBricks:Node[] = []
    //游戏状态
    public _gameStatus:EnumGameStatus = 0;
///////////////////////////////////////////////
    //更新、获取游戏状态
    set gameStatus(status:EnumGameStatus){
        switch(status){
            case EnumGameStatus.init://初始化
                console.log("Status:init");
                this.scheduleOnce(()=>{
                    this.gameStatus=EnumGameStatus.idle;
                },0.2)//延迟0.2s后切换状态
                break;
            case EnumGameStatus.idle:
                console.log("Status:idle");
                break;
            case EnumGameStatus.pause:
                console.log("Status:pause");
                break;
            case EnumGameStatus.die:
                console.log("Status:die");
                break;
            default:
                console.log("Error:Wrong game status!")
                this.gameStatus=EnumGameStatus.die;
        }
        this._gameStatus=status;
    }
    get gameStatus(){
        return this._gameStatus;
    }
///////////////////////////////////////////////
    private summonBricks(){//生成砖块
        let rand = math.randomRange(-200,200);//随机数
        const newBrick=instantiate(this.pref_Brick);//实例化
        this.nd_BrickPlace.addChild(newBrick);//添加子节点
        newBrick.setPosition(v3(0,rand,0));//设置节点的相对位置
        this.allBricks.push(newBrick)//入队尾
        if(this.allBricks.length>8){//判断队列长度
            this.allBricks.shift().destroy()//队首出队并销毁
        }
        tween(newBrick)//移动
        .to(20,{position:v3(-2000,rand,0)})
        .delay(500)
        .start();
    }

    private checkTimer(){//每一定间隔生成砖块
        if(this.Timer<300){
            this.Timer++;
        }
        else{
            this.Timer=0;
            this.summonBricks();
            console.log("Summon")
        }
    }
///////////////////////////////////////////////
    start() {
        this.summonBricks();
        this.gameStatus=EnumGameStatus.init;//初始化游戏
    }

    update(deltaTime: number) {
        this.checkTimer();
    }
}
